@import url(https://fonts.googleapis.com/css?family=Montserrat:400,400i,800i,900|Share+Tech+Mono);
@import url(https://fonts.googleapis.com/css?family=Montserrat:400,400i,800i,900|Share+Tech+Mono);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

table, th, td {
  border: 1px solid black;
  border-collapse: collapse;
}

body {
  background-color:#C1C8E4;
  /*background: radial-gradient(circle, rgba(238,174,202,1) 0%, rgba(148,187,233,1) 100%);*/
  background: -webkit-radial-gradient(circle, rgba(250,255,184,1) 0%, rgba(81,181,164,1) 100%);
  background: radial-gradient(circle, rgba(250,255,184,1) 0%, rgba(81,181,164,1) 100%);
}


a href {
  color: darkblue;
  background: -webkit-linear-gradient(blue,purple );
  -webkit-background-clip: text;
  /*-webkit-text-fill-color: transparent;*/
  font-family: Share Tech Mono, monospace;
}

h1 {
  background: -webkit-linear-gradient(black,purple );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
}



